		<div>
			<label for="">{{ title }}</label>
		</div>
		<input type="text" v-model="text" class="w-100" />
		<p class="text-danger" v-show="error">Error</p>
	</div>
</template>

<script>
export default {
	name: "TextInput",
	props: {
		title: {
			type: String,
			default: "No Title",
		},
		text: {
			type: String,
			default: "",
		},
        error: {
			type: String,
			default: "",
		},
	},
};
</script>

