<template>
	<div class="theme-dashboard-view">
		<div class="d-flex justify-content-between">
			<!-- Dashboard Title -->
			<h1 v-show="!edit">{{ $t("inventory_create_title") }}</h1>
			<h1 v-show="edit">{{ $t("inventory_update_title") }}</h1>

			<!-- Submit Button -->
			<div style="height: 50px; width: 120px" v-show="!loading">
				<button
					class="theme-button theme-background-light text-dark"
					style="font-weight: 400"
					@click="this.edit ? updateProduct() : createProduct()"
				>
					{{ $t("inventory_create_save_button") }}
				</button>
			</div>
		</div>
		<!-- Dashboard Content -->
		<div class="my-4">
			<!-- Results -->
			<div>
				<div class="d-flex justify-content-center">
					<LoadingIndicator :active="loading" />
				</div>
				<div v-show="message">
					<div class="justify-content-center text-center my-4">
						<h5>{{ message }}</h5>
					</div>
				</div>
			</div>

			<!-- Create Product Form -->
			<div v-show="!loading">
				<TabView :tabs="Object.keys(tabs)" :tabText="tabs">
					<!-- General Tab -->
					<template v-slot:General>
						<div class="p-5" style="min-height: 52vh">
							<h4>{{ $t("inventory_create_general_title") }}</h4>
							<div class="d-flex flex-wrap">
								<div class="col-6">
									<!-- Item name -->
									<div class="w-25 my-3">
										<label>
											{{
												$t(
													"inventory_create_general_item_name"
												)
											}}
										</label>
										<input
											type="text"
											v-model="itemName"
											class="w-100 p-2"
											:placeholder="
												$t(
													'inventory_create_general_item_name_placeholder'
												)
											"
										/>
									</div>

									<!-- Point of Sale ID -->
									<div>
										<div>
											<label for=""
												>Point of Sale ID</label
											>
										</div>
										<input
											type="text"
											v-model="itemPOSID"
										/>
									</div>

									<!-- Point of Sale Name  -->
									<div>
										<div>
											<label for=""
												>Point of Sale Name</label
											>
										</div>
										<input
											type="text"
											v-model="itemPOSName"
										/>
									</div>
								</div>
								<!-- Side bar -->
								<div class="col-6">
									<!-- Almondi Quanity -->
									<div>
										<div>
											<label>
												{{
													$t(
														"inventory_create_general_item_amoudi_quantity"
													)
												}}
											</label>
										</div>
										<div class="d-flex">
											<span
												class="
													px-2
													d-flex
													align-items-center
												"
												style="cursor: pointer"
												@click="
													() => {
														this.itemAmoudiQuantity -= 1;
													}
												"
											>
												<i
													class="fa fa-minus"
													aria-hidden="true"
													style="font-size: 2vh"
												></i>
											</span>

											<input
												type="number"
												placeholder
												min="0"
												v-model="itemAmoudiQuantity"
												class="text-center"
												style="border: none"
											/>
											<span
												class="
													px-2
													d-flex
													align-items-center
												"
												style="cursor: pointer"
												@click="
													() => {
														this.itemAmoudiQuantity += 1;
													}
												"
											>
												<i
													class="fa fa-plus"
													aria-hidden="true"
													style="font-size: 2vh"
												></i>
											</span>
										</div>
									</div>
									<!-- Nani Quantity -->
									<div>
										<div>
											<label>
												{{
													$t(
														"inventory_create_general_item_nani_quantity"
													)
												}}
											</label>
										</div>
										<div class="d-flex">
											<span
												class="
													px-2
													d-flex
													align-items-center
												"
												style="cursor: pointer"
												@click="
													() => {
														this.itemNaniQuantity -= 1;
													}
												"
											>
												<i
													class="fa fa-minus"
													aria-hidden="true"
													style="font-size: 2vh"
												></i>
											</span>

											<input
												type="number"
												placeholder
												min="0"
												v-model="itemNaniQuantity"
												class="text-center"
												style="border: none"
											/>
											<span
												class="
													px-2
													d-flex
													align-items-center
												"
												style="cursor: pointer"
												@click="
													() => {
														this.itemNaniQuantity += 1;
													}
												"
											>
												<i
													class="fa fa-plus"
													aria-hidden="true"
													style="font-size: 2vh"
												></i>
											</span>
										</div>
									</div>
									<!-- Sara Quantity -->
									<div>
										<div>
											<label>
												{{
													$t(
														"inventory_create_general_item_sara_quantity"
													)
												}}
											</label>
										</div>
										<div class="d-flex">
											<span
												class="
													px-2
													d-flex
													align-items-center
												"
												style="cursor: pointer"
												@click="
													() => {
														this.itemSaraQuantity -= 1;
													}
												"
											>
												<i
													class="fa fa-minus"
													aria-hidden="true"
													style="font-size: 2vh"
												></i>
											</span>

											<input
												type="number"
												placeholder
												min="0"
												v-model="itemSaraQuantity"
												class="text-center"
												style="border: none"
											/>
											<span
												class="
													px-2
													d-flex
													align-items-center
												"
												style="cursor: pointer"
												@click="
													() => {
														this.itemSaraQuantity += 1;
													}
												"
											>
												<i
													class="fa fa-plus"
													aria-hidden="true"
													style="font-size: 2vh"
												></i>
											</span>
										</div>
									</div>

									<!-- Increment -->
									<div>
										<div>
											<label>
												{{ $t("inventory_increment") }}
											</label>
										</div>
										<input
											type="number"
											min="0.00"
											max="10000.00"
											step="0.01"
											v-model="itemIncrement"
										/>
									</div>

									<!-- Unit -->
									<div>
										<div>
											<label>Unit</label>
										</div>
										<select v-model="itemUnit">
											<option :value="0">Unit</option>
											<option :value="1">Mass</option>
											<option :value="2">Volume</option>
										</select>
									</div>
								</div>
							</div>

							<!-- <div class="w-50 my-3">
                    <TextAreaInput class="" title="Item Description"/>
							</div>-->
						</div>
					</template>

					<!-- Category Tab -->
					<template v-slot:Category>
						<div class="p-5" style="min-height: 52vh">
							<h4>{{ $t("inventory_create_category_title") }}</h4>
							<!-- Picked Category -->
							<div class="my-4">
								<p class="m-0">
									{{
										$t(
											"inventory_create_category_selected_category"
										)
									}}
								</p>
								<!-- Category Picker -->
								<!-- TODO: Category Picker With Search -->
								<div
									class="
										border
										p-3
										rounded
										selectBox
										d-flex
										justify-content-between
									"
									@click="toggleCategoryList"
									style="cursor: pointer"
								>
									<div>
										{{
											categoryList.length > 0
												? categoryList[selectedCategory]
														.name
												: "none"
										}}
									</div>

									<!-- Down Icon -->
									<div
										class="
											d-flex
											align-items-center
											col-1
											justify-content-center
										"
									>
										<i
											class="fa"
											:class="
												displayCategoryList
													? 'fa-chevron-down'
													: 'fa-chevron-left'
											"
											aria-hidden="true"
										></i>
									</div>
								</div>
							</div>

							<!-- Category List -->
							<div class="bg-light" v-show="displayCategoryList">
								<div class="d-flex justify-content-center py-4">
									<input
										type="search"
										placeholder="Search"
										class="p-2"
										style="width: 600px"
										v-model="CategorySearchText"
										@keydown.enter="getCategories(1)"
									/>
									<button
										class="
											theme-background-primary
											text-light
											py-2
											px-3
										"
										style="border: none"
										@click="getCategories(1)"
									>
										<i class="fa fa-search"></i>
									</button>
								</div>

								<div
									class="
										border
										d-flex
										flex-wrap
										align-items-center
										justify-content-center
									"
								>
									<!-- Category list-->
									<div
										v-for="(
											category, index
										) in categoryList"
										:key="index"
										class="
											mx-3
											my-2
											rounded
											d-flex
											category
											align-items-center
											justify-content-center
										"
										:class="
											index == selectedCategory
												? 'selected'
												: ''
										"
										@click="
											() => {
												this.itemCategory = category.id;
												this.selectedCategory = index;
												this.displayCategoryList = false;
											}
										"
									>
										<div>
											<div
												class="h5"
												style="font-weight: 500"
											>
												{{ category.name }}
											</div>

											<div>
												{{ category.description }}
											</div>
										</div>
									</div>
									<div class="d-flex justify-content-center">
										<PageNav
											:pages="category_pages"
											:current_page="
												category_current_page
											"
											@click-page="getCategories"
											@next-page="nextPage"
											@prev-page="prevPage"
											@goto-first="gotoFirstPage"
											@goto-last="gotoLastPage"
										/>
									</div>
								</div>
							</div>

							<!-- Tags Input Form -->
							<div class="form-group">
								{{ $t("category_fields_tags") }}
								<div class="p-2 d-flex flex-wrap">
									<div
										class="
											theme-background-secondary
											border
											rounded
											d-flex
											mx-2
										"
										v-for="(tag, index) in itemTags"
										:key="index"
									>
										<span class="p-2 mx-1">{{ tag }}</span>

										<span
											class="px-1"
											@click="removeTag(index)"
											style="cursor: pointer"
											>x</span
										>
									</div>
								</div>
								<input
									type="text"
									@keydown.enter="addTag"
									v-model="tagText"
									:placeholder="
										$t('category_fields_tags_placeholder')
									"
									class="p-2"
								/>
							</div>
						</div>
					</template>

					<!-- Pricing Tab -->
					<template v-slot:Price>
						<div class="p-5" style="min-height: 52vh">
							<h4>{{ $t("inventory_create_price_title") }}</h4>
							<!-- Set Price -->
							<div>
								<div>
									<label>
										{{
											$t(
												"inventory_create_price_option_title"
											)
										}}
									</label>
								</div>
								<input
									type="number"
									min="0.00"
									max="10000.00"
									step="0.01"
									v-model="itemPrice"
								/>
							</div>

							<!-- Max Orders -->
							<div>
								<div>
									<label>
										{{ $t("inventory_max_order") }}
									</label>
								</div>
								<input
									type="number"
									min="0.00"
									max="10000.00"
									step="0.01"
									v-model="itemMaximumOrderQuantity"
								/>
							</div>
							<!-- Min Orders -->
							<div>
								<div>
									<label>
										{{ $t("inventory_min_order") }}
									</label>
								</div>
								<input
									type="number"
									min="0.00"
									max="10000.00"
									step="0.01"
									v-model="itemMinimumOrderQuantity"
								/>
							</div>

							<!-- Set Discount Dates -->
							<!-- <div class="my-3">
								<p class="m-0" style="font-weight: bold">
									Discount
								</p>
								<div class="my-2">
									<div><label>Discount Start Date</label></div>
									<input type="date" />
								</div>
								<div class="my-2">
									<div>
										<label>Discount Price</label>
									</div>
									
									<input
										type="number"
										min="0.00"
										max="10000.00"
										step="0.01"
									/>
								</div>
								<div class="my-2">
									<div><label>Discount End Date</label></div>
									<input type="date" />
								</div>
							</div>-->
						</div>
					</template>


					<!-- Image Tab -->
					<template v-slot:Image>
						<div class="p-5" style="min-height: 52vh">
							<div class="d-flex justify-content-between">
								<h4>
									{{ $t("inventory_create_image_title") }}
								</h4>
								<label
									class="
										theme-button
										d-flex
										align-items-center
										justify-content-center
									"
									style="width: 170px; height: 60px"
								>
									<i
										class="fa fa-image mx-2"
										style="font-size: 20px"
									></i>
									{{
										$t("inventory_create_image_select_text")
									}}
									<input
										type="file"
										style="display: none"
										name="image"
										@change="setImage"
										accept="image/*"
									/>
								</label>
							</div>

							<div
								class="my-3 border d-flex flex-wrap p-2"
								style="min-height: 300px"
							>
								<div
									:class="{
										'd-flex': itemImages.length == 0,
										'd-none': itemImages.length != 0,
									}"
									class="
										w-100
										align-items-center
										justify-content-center
									"
								>
									<p>
										{{ $t("inventory_create_image_alt") }}
									</p>
								</div>
								<!-- Image Generator -->
								<div
									class="d-flex border m-2"
									style="width: 350px"
									v-for="(image, index) in itemImages"
									:key="index"
								>
									<img class="w-100 h-100" :src="image" />
								</div>
							</div>
						</div>
					</template>
				</TabView>
			</div>
		</div>
	</div>
</template>

<script>
// Components
import TextInput from "@/components/TextInput.vue";
import TextAreaInput from "@/components/TextAreaInput.vue";
import TabView from "@/components/TabView.vue";
import LoadingIndicator from "@/components/LoadingIndicator.vue";
import PageNav from "@/components/PageNav.vue";

// Services
import SearchService from "@/services/search.js";
import CategoryService from "@/services/category.js";
import ProductService from "@/services/products.js";

export default {
  components: {
    TextInput,
    TextAreaInput,
    TabView,
    LoadingIndicator,
    PageNav,
  },
  data() {
    return {
      // Product Data
      itemName: "",
      itemPOSID: 0,
      itemPOSName: "",
      itemPrice: 0.0,
      itemAmoudiQuantity: 0,
      itemNaniQuantity: 0,
      itemSaraQuantity: 0,
      itemIncrement: 0,
      itemUnit: 0,
      itemMaximumOrderQuantity: 0,
      itemMinimumOrderQuantity: 0,
      itemCategory: 0,
      itemImage: "",
      itemImages: [],
      itemTags: [],

      // Display Data
      tabs: {
        // Translated Tab Text
        General: this.$t("inventory_create_general_title"),
        Category: this.$t("inventory_create_category_title"),
        Price: this.$t("inventory_create_price_title"),
        Image: this.$t("inventory_create_image_title"),
      },

      // Category Parameters
      categoryList: [],
      tagText: "",
      displayCategoryList: false,
      selectedCategory: 0,
      category_current_page: 1,
      category_pages: 0,
      CategorySearchText: "",

      // Response Indicator
      loading: false,
      message: "",
      edit: false,
    };
  },

  methods: {
    // Creates the product
    createProduct() {
      this.loading = true;
      this.message = "";

      // Check if valid inputs
      if (!this.createProductValidate()) {
        this.loading = false;
        return;
      }
      console.log({
        name: this.itemName,
        amoudi_quantity: this.itemAmoudiQuantity,
        nani_quantity: this.itemNaniQuantity,
        sara_quantity: this.itemSaraQuantity,
        increment: this.itemIncrement,
        minimum_order_quantity: this.itemMinimumOrderQuantity,
        maximum_order_quantity: this.itemMaximumOrderQuantity,
        unit: this.itemUnit,

        // to be changed
        pos_id: this.itemPOSID,
        pos_name: this.itemPOSName,

        img: this.itemImages,
        tags: this.itemTags,
        price: this.itemPrice,
        category: this.itemCategory,
      });
      ProductService.createProduct({
        name: this.itemName,
        amoudi_quantity: this.itemAmoudiQuantity,
        nani_quantity: this.itemNaniQuantity,
        sara_quantity: this.itemSaraQuantity,
        increment: this.itemIncrement,
        minimum_order_quantity: this.itemMinimumOrderQuantity,
        maximum_order_quantity: this.itemMaximumOrderQuantity,
        unit: this.itemUnit,

        // to be changed
        pos_id: this.itemPOSID,
        pos_name: this.itemPOSName,

        img: this.itemImages,
        tags: this.itemTags,
        price: this.itemPrice,
        category: this.itemCategory,
      }).then(
        (response) => {
          this.loading = false;
          this.message = response.data.message;
          this.emptyValues();
        },
        (error) => {
          this.loading = false;
          console.log(error.response);
          this.message = error.response.data.message;
        }
      );
    },
    // Update the product
    updateProduct() {
      this.loading = true;
      this.message = "";

      if (!this.createProductValidate()) {
        this.loading = false;
        return;
      }

      ProductService.updateProduct(this.$route.params.id, {
        name: this.itemName,
        amoudi_quantity: this.itemAmoudiQuantity,
        nani_quantity: this.itemNaniQuantity,
        sara_quantity: this.itemSaraQuantity,
        unit: this.itemUnit,
        img: this.itemImages,
        tags: this.itemTags,
        price: this.itemPrice,
        category: this.itemCategory,
        pos_id: this.itemPOSID,
        pos_name: this.itemPOSName,
        increment: this.itemIncrement,
        minimum_order_quantity: this.itemMinimumOrderQuantity,
        maximum_order_quantity: this.itemMaximumOrderQuantity,
      }).then(
        (response) => {
          this.loading = false;
          console.log(response);
          this.message = "Product Updated!!";
        },
        (error) => {
          this.loading = false;
          console.log(error.response);
          this.message = error.response.data.message;
        }
      );
    },
    // Set the image
    setImage(e) {
      const selectedImage = e.target.files[0];
      const reader = new FileReader();

      // Set itemImage once processing is done
      reader.onload = (e) => {
        this.itemImages.push(e.target.result);
      };

      reader.readAsDataURL(selectedImage);
    },
    // Validates the product creation
    createProductValidate() {
      if (this.itemName == "") {
        this.message = "Item name should not be empty";
        return false;
      }
      if (this.itemQuantity < 1) {
        this.message = "Item Quantity cannot be zero";
        return false;
      }
      return true;
    },
    // Empties values once a product has been created
    emptyValues() {
      this.itemName = "";
      this.itemPrice = 0.0;
      this.amoudi_quantity = 0;
      this.nani_quantity = 0;
      this.sara_quantity = 0;
      this.itemCategory = 0;
      this.itemImages = [];
      this.itemTags = [];
      this.selectedCategory = 0;
      this.itemPOSID = 0;
      this.itemPOSName = "";
    },
    // Get the category
    getCategoryIndex(id) {
      for (var i = 0; i <= this.categoryList.length; i++) {
        if (this.categoryList[i] != null) {
          if (this.categoryList[i].id == id) {
            return i;
          }
        }
      }
      return 0;
    },
    // Toggle the category list
    toggleCategoryList() {
      this.displayCategoryList = !this.displayCategoryList;
    },
    // Add Tags
    addTag() {
      this.itemTags.push(this.tagText);
    },
    // Remove Tags
    removeTag(index) {
      this.itemTags.splice(index, 1);
      console.log(this.itemTags);
    },

    // Get Categories
    getCategories(page) {
      // If the category is search display search results
      if (this.CategorySearchText.length != 0) {
        this.CategorySearch(page);
        return;
      }
      // If not search just let user browse through pagination
      this.loading = true;
      this.message = "";

      CategoryService.getCategories(page).then(
        (response) => {
          this.loading = false;
          this.categoryList = response.data.data.categories;
          this.category_current_page = page;
          this.category_pages = response.data.data.total_pages;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },

    // Category Search
    CategorySearch(page) {
      this.loading = true;
      this.message = "";
      console.log(page, {
        search: this.CategorySearchText,
        category_id: null,
        tags: [],
        price_g: null,
        price_l: null,
      });
      SearchService.SearchProduct(
        page,
        {
          search: this.CategorySearchText,
          category_id: null,
          tags: [],
          price_g: null,
          price_l: null,
        },
        "category"
      ).then(
        (response) => {
          this.loading = false;
          this.category_current_page = page;
          this.categoryList = response.data.data.categories;
          this.category_pages = response.data.data.total_pages;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
    // Category page navigation
    // Page Navigation
    nextPage() {
      if (this.category_current_page != this.category_pages) {
        this.getCategories(this.category_current_page + 1);
      }
    },
    prevPage() {
      if (this.category_current_page != 1) {
        this.getCategories(this.category_current_page - 1);
      }
    },
    gotoFirstPage() {
      if (this.category_current_page != 1) {
        this.getCategories(1);
      }
    },
    gotoLastPage() {
      if (this.category_current_page != this.category_pages) {
        this.getCategories(this.category_pages);
      }
    },
  },
  mounted() {
    // Check if there is a id and set to edit mode
    if (this.$route.params.id != null) {
      this.edit = true;
      this.loading = true;
      this.message = "";
      // Get the product and set the values

      ProductService.getSingleProduct(this.$route.params.id).then(
        (response) => {
          this.loading = false;
          const product = response.data.data;
          this.itemName = product.name;
          this.itemPOSID = product.pos_id;
          this.itemPOSName = product.pos_name;
          this.itemIncrement = product.increment;
          this.itemUnit = product.unit;
          this.itemMaximumOrderQuantity = product.maximum_order_quantity;
          this.itemMinimumOrderQuantity = product.minimum_order_quantity;
          this.itemPrice = product.price;
          this.itemAmoudiQuantity = product.amoudi_quantity;
          this.itemNaniQuantity = product.nani_quantity;
          this.itemSaraQuantity = product.sara_quantity;
          this.itemCategory = product.category_id;
          this.itemTags = product.tags;
          this.itemImages = [];
          // Get the categories for the product
          const page = Math.ceil(this.itemCategory / 15);
          CategoryService.getCategories(page).then((response) => {
            this.categoryList = response.data.data.categories;
            this.category_current_page = page;
            this.category_pages = response.data.data.total_pages;
            this.selectedCategory = this.getCategoryIndex(this.itemCategory);
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    } else {
      // Get the categories for the product
      // CategoryService.getCategories(1).then((response) => {
      // 	this.categoryList = response.data.data.categories;
      // });
      this.getCategories(1);
    }
  },
  updated() {
    if (this.edit) {
      console.log("Update");
      if (this.$route.params.id == null) {
        this.edit = false;
        this.emptyValues();
      }
    }
  },
};
</script>

